





















import { Component, Vue, Prop } from "vue-property-decorator";

// images
import LogoSrc from "@/assets/icons/logo.svg";
import LogoSrcDark from "@/assets/icons/logo--dark.svg";

// types
interface MenuType {
  text: string;
  link: string;
}

@Component({
  components: {},
})
export default class MainNav extends Vue {
  @Prop({
    type: Array,
    default() {
      return [
        {
          link: "/",
          text: "Каталог",
        },
        {
          link: "/",
          text: "Доставка",
        },
        {
          link: "/",
          text: "Оплата",
        },
        {
          link: "/",
          text: "Контакты",
        },
        {
          link: "/",
          text: "О галерее",
        },
      ];
    },
  })
  menuList?: Array<MenuType>;

  @Prop({
    type: Boolean,
    default: true,
  })
  withLogo?: boolean;

  @Prop({
    type: String,
    default: "light",
    validator(val) {
      return val === "light" || val === "dark";
    },
  })
  logoType?: string;

  get logoSrc() {
    return this.logoType === "light" ? LogoSrc : LogoSrcDark;
  }
}
